body{margin:0px;}
.form{
	position: absolute;
	width: 100%;
	height: 100%;
}
.head_block{
	position: absolute;
	background:grey;
	width: 100%;
	height: 10%;
}

.filter_block{
	position:absolute;
	background: #DBDCDC;
	width: 25%;
	height: 60%;
	top: 10%;
	overflow: auto;
	
}
.checkGroup{
	position:absolute;
	left: 10%;
	margin-top: 15px;
	overflow: auto;
}
.btn_build{
	margin-top:5%;
	margin-left: 10%
	
}
.result_block{
	position:absolute;
	background: #9ACCCC;
	width: 25%;
	height: 30%;
	top: 70%;
	box-shadow: 0 0 5px;
}
.table_block{
	position:absolute;
	background-image:url(images/back_table.png);
	width: 75%;
	height: 90%;
	top: 10%;
	left:25%;
	box-shadow: inset 0 0 6px;
}
.build_table_btn{
	position: absolute;
	left: 15%;
	top: 0%
}

.tableOut{
	position:absolute;
	background: white;
	width:100%;
	height:100%;
	overflow: auto;
	box-shadow: inset 0 0 6px;
}
.preloader{
	position:absolute;
	background-color: rgba(0, 0, 0,0.3);
	width:100%;
	height:100%;
	box-shadow: inset 0 0 6px;
	z-index:5;
}
.Select_div{
	position:absolute;
	margin-left: 10px;
	margin-top: 0.5%;
}
.Instruction{
	position:absolute;
	left: 200px;
	height:90%;
	margin-top:0.5%;
}
.btn_instruction{
	height:70%;
	background: #e9f0f5;
  background-image: -webkit-linear-gradient(top, #e9f0f5, #f2f8fc);
  background-image: -moz-linear-gradient(top, #e9f0f5, #f2f8fc);
  background-image: -ms-linear-gradient(top, #e9f0f5, #f2f8fc);
  background-image: -o-linear-gradient(top, #e9f0f5, #f2f8fc);
  background-image: linear-gradient(to bottom, #e9f0f5, #f2f8fc);
  -webkit-border-radius: 10;
  -moz-border-radius: 10;
  border-radius: 10px;
  font-family: Arial;
  color: #140114;
  font-size: 20px;
  padding: 1% 20px 1% 20px;
  text-decoration: none;
}
.btn_instruction:hover{
	background: #d1dee6;
  background-image: -webkit-linear-gradient(top, #d1dee6, #d3e4f0);
  background-image: -moz-linear-gradient(top, #d1dee6, #d3e4f0);
  background-image: -ms-linear-gradient(top, #d1dee6, #d3e4f0);
  background-image: -o-linear-gradient(top, #d1dee6, #d3e4f0);
  background-image: linear-gradient(to bottom, #d1dee6, #d3e4f0);
  text-decoration: none;
}
.selects{
   display: inline-block;
  width: 160px;
  height: 34px;
  line-height: 30px;
  position: relative;
}
